import axios from 'axios'
import MensagemUtil from '@/util/MensagemUtil'
export default {
    criar (filtro) {
            return axios.post('/revisao', filtro)
            .then(function (response) {
                MensagemUtil.sucesso('Reviso cadastrada')
                return response.data
            })
            .catch(function (error) {
                MensagemUtil.erro('Erro ao tentar criar Reviso')
                console.error(error)
            })
    },
    atualizar (revisao) {
            return axios.put('/revisao', revisao)
            .then(function (response) {
                return response.data
            })
            .catch(function (error) {
                MensagemUtil.erro('Erro ao tentar atualizar Reviso')
                console.error(error)
            })
    },
    editar (revisao) {
        return axios.get('/revisao/' + revisao.id)
        .then(function (response) {
            return response.data
        })
        .catch(function (error) {
            MensagemUtil.erro('Erro ao tentar editar Reviso')
            console.error(error)
        })
    },
    remover (revisao) {
        return axios.delete('/revisao/' + revisao.id)
        .then(function (response) {
            MensagemUtil.sucesso('Reviso removida')
            return response.data
        })
        .catch(function (error) {
            MensagemUtil.erro('Erro ao tentar remover Reviso')
            console.error(error)
        })
    },
    listarTodos () {
        return axios.get('/revisao/listarTodos')
        .then(function (response) {
            return response.data
        })
        .catch(function (error) {
            MensagemUtil.erro('Erro ao tentar listar Revises')
            console.error(error)
        })
    }
}



